import React ,{Component} from 'react';
import Scroll from 'react-bscroll';
class TBody extends Component{
    render(){
        let {userList,allPrice}=this.props;
        return <div style={{position:"relative",height:"460px"}}>
                    <Scroll>
                        <ul className="computedList">
                                    <li>
                                        <span></span>
                                        <span></span>
                                        <span></span>
                                        <span></span>
                                        <span></span>
                                    </li>
                                    {
                                        userList.map((item,key)=>{
                                            let n=(allPrice/userList.length).toFixed(2);
                                    return  <li key={key}>
                                                <span>{item.name}</span>
                                                <span>{item.money*1}</span>
                                                <span>{n}</span>
                                                <span>{(item.money*1-n)<0?Math.abs((item.money*1-n)):0}</span>
                                                <span>{(item.money*1-n)>0?(item.money*1-n).toFixed(2):0}</span>
                                            </li>
                                        })
                                    }
                            </ul>
                    </Scroll>
                </div>
    }
}
export default TBody;


